import { CopyOutlined, MinusOutlined, PlusOutlined } from '@ant-design/icons';
import { PageContainer } from '@ant-design/pro-components';
import {
  Button,
  Card,
  Checkbox,
  Col,
  Divider,
  Flex,
  Form,
  Input,
  message,
  Row,
  Select,
  Space,
  Spin,
} from 'antd';
import React, { useEffect, useState } from 'react';

import { callDeepSeekAPI, type GenerateRequest } from '@/services/deepseek';
import { mockGenerate } from '@/services/mockData';

const { TextArea } = Input;

const SunoCover: React.FC = () => {
  // 
  const [form] = Form.useForm<GenerateRequest>();

  // 
  const [loading, setLoading] = useState(false);
  const [stylesResult, setStylesResult] = useState('');
  const [lyricsResult, setLyricsResult] = useState('');
  const [isApiKeyVisible, setIsApiKeyVisible] = useState(false);

  // localStorageAPI Key
  useEffect(() => {
    const savedApiKey = localStorage.getItem('deepseekApiKey');
    if (savedApiKey) {
      form.setFieldsValue({
        apiKey: savedApiKey,
        rememberApiKey: true,
      });
    }
  }, [form]);

  // 
  const handleSubmit = async (values: GenerateRequest) => {
    // API KeylocalStorage
    if (values.rememberApiKey) {
      localStorage.setItem('deepseekApiKey', values.apiKey);
    } else {
      localStorage.removeItem('deepseekApiKey');
    }

    // API
    setLoading(true);
    try {
      const result = await callDeepSeekAPI(values);
      setStylesResult(result.styles);
      setLyricsResult(result.lyrics);
      message.success('');
    } catch (error) {
      // 
      console.error('API:', error);
      const errorMessage =
        error instanceof Error
          ? error.message
          : 'DeepSeek APIAPI Key';
      message.error(errorMessage);
    } finally {
      setLoading(false);
    }
  };

  // 
  const copyToClipboard = (text: string, type: string) => {
    navigator.clipboard
      .writeText(text)
      .then(() => {
        message.success(`${type}`);
      })
      .catch(() => {
        message.error('');
      });
  };

  // mockGenerate
  const handleMockGenerate = async () => {
    setLoading(true);
    try {
      // 
      const result = await mockGenerate();
      // 
      setStylesResult(result.styles);
      setLyricsResult(result.lyrics);
      message.success('');
    } catch (error) {
      console.error(':', error);
      message.error('');
    } finally {
      setLoading(false);
    }
  };

  // 
  // 
  // 
  // false
  const isProduction = false;

  return (
    <PageContainer>
      {/*  */}
      <Spin
        spinning={loading}
        fullscreen
        tip=""
        size="large"
      />
      <Row gutter={[24, 0]}>
        {/*  */}
        <Col span={8}>
          <Card
            title=""
            actions={[
              // div
              <Flex
                key="buttons"
                vertical
                gap="small"
                style={{ width: '100%', padding: '0 16px' }}
              >
                {/*  */}
                <Button
                  type="primary"
                  onClick={() => form.submit()}
                  loading={loading}
                  size="large"
                  block
                >
                  
                </Button>

                {/*  */}
                {!isProduction && (
                  <Button
                    onClick={handleMockGenerate}
                    loading={loading}
                    size="large"
                    type="text"
                    block
                  >
                    
                  </Button>
                )}
              </Flex>,
            ]}
          >
            <Form
              form={form}
              layout="vertical"
              onFinish={handleSubmit}
              initialValues={{
                song_language: 'Mandarin',
                reference_songs: [{ title: '', artist: '' }],
                rememberApiKey: false,
              }}
            >
              {/* DeepSeek API Key */}
              <Form.Item
                name="apiKey"
                label="DeepSeek API Key"
                rules={[{ required: true, message: 'DeepSeek API Key' }]}
              >
                <Input.Password
                  placeholder="DeepSeek API Key"
                  visibilityToggle={{
                    visible: isApiKeyVisible,
                    onVisibleChange: setIsApiKeyVisible,
                  }}
                />
              </Form.Item>

              <Form.Item name="rememberApiKey" valuePropName="checked">
                <Checkbox></Checkbox>
              </Form.Item>

              <Divider />

              {/*  */}
              <Form.Item
                name="song_language"
                label=""
                rules={[{ required: true, message: '' }]}
              >
                <Select placeholder="">
                  <Select.Option value="Mandarin"></Select.Option>
                  <Select.Option value="Cantonese"></Select.Option>
                  <Select.Option value="Minnan"></Select.Option>
                  <Select.Option value="English"></Select.Option>
                  <Select.Option value="Korean"></Select.Option>
                  <Select.Option value="Japanese"></Select.Option>
                  <Select.Option value="Other"></Select.Option>
                </Select>
              </Form.Item>

              {/*  */}
              <Form.Item
                name="target_artist"
                label=""
                rules={[{ required: true, message: '' }]}
              >
                <Input placeholder="" />
              </Form.Item>

              {/*  */}
              <Form.List
                name="reference_songs"
                rules={[
                  {
                    validator: (_, songs) => {
                      if (songs && songs.length > 3) {
                        return Promise.reject(new Error('3'));
                      }
                      return Promise.resolve();
                    },
                  },
                ]}
              >
                {(fields, { add, remove }, { errors }) => (
                  <>
                    <Form.Item label="0-3">
                      <Space
                        direction="vertical"
                        style={{ width: '100%' }}
                        size="middle"
                      >
                        {fields.map(({ key, name, ...restField }) => (
                          <Space
                            key={key}
                            style={{ width: '100%' }}
                            align="center"
                            size="middle"
                          >
                            <Form.Item
                              {...restField}
                              name={[name, 'title']}
                              rules={[
                                { required: true, message: '' },
                              ]}
                              style={{ flex: 1, marginBottom: 0 }}
                            >
                              <Input
                                placeholder=""
                                style={{ width: '100%' }}
                              />
                            </Form.Item>
                            <Form.Item
                              {...restField}
                              name={[name, 'artist']}
                              style={{ flex: 1, marginBottom: 0 }}
                            >
                              <Input
                                placeholder=""
                                style={{ width: '100%' }}
                              />
                            </Form.Item>
                            <Form.Item style={{ marginBottom: 0 }}>
                              <Button
                                type="text"
                                danger
                                icon={<MinusOutlined />}
                                onClick={() => remove(name)}
                                disabled={fields.length === 1}
                                size="small"
                              >
                                
                              </Button>
                            </Form.Item>
                          </Space>
                        ))}
                        <Form.Item>
                          <Button
                            type="dashed"
                            onClick={() => add()}
                            disabled={fields.length >= 3}
                            block
                            icon={<PlusOutlined />}
                          >
                            
                          </Button>
                        </Form.Item>
                      </Space>
                    </Form.Item>
                    <Form.ErrorList errors={errors} />
                  </>
                )}
              </Form.List>

              {/*  */}
              <Form.Item name="style_note" label="">
                <TextArea
                  placeholder=""
                  rows={3}
                />
              </Form.Item>

              {/*  */}
              <Form.Item
                name="extra_note"
                label="/"
              >
                <TextArea
                  placeholder=""
                  rows={2}
                />
              </Form.Item>

              {/*  */}
              <Form.Item
                name="lyrics_raw"
                label=""
                rules={[
                  { required: true, message: '' },
                  { max: 2000, message: '2000' },
                ]}
              >
                <TextArea
                  placeholder="[Verse][Chorus] "
                  rows={8}
                />
              </Form.Item>
            </Form>
          </Card>
        </Col>

        {/* Styles */}
        <Col span={8}>
          <Card
            title="StylesSuno"
            extra={
              <Button
                type="text"
                icon={<CopyOutlined />}
                onClick={() => copyToClipboard(stylesResult, 'Styles')}
                disabled={!stylesResult}
                size="small"
              >
                
              </Button>
            }
            style={{ marginBottom: 24 }}
          >
            <TextArea
              value={stylesResult}
              readOnly
              placeholder="Styles..."
              style={{ height: '100%', resize: 'none' }}
              autoSize={{ minRows: 40, maxRows: Infinity }}
            />
          </Card>
        </Col>

        {/* Lyrics */}
        <Col span={8}>
          <Card
            title="LyricsSuno"
            extra={
              <Button
                type="text"
                icon={<CopyOutlined />}
                onClick={() => copyToClipboard(lyricsResult, 'Lyrics')}
                disabled={!lyricsResult}
                size="small"
              >
                
              </Button>
            }
            style={{ marginBottom: 24 }}
          >
            <TextArea
              value={lyricsResult}
              readOnly
              placeholder="Lyrics..."
              style={{ height: '100%', resize: 'none' }}
              autoSize={{ minRows: 40, maxRows: Infinity }}
            />
          </Card>
        </Col>
      </Row>
    </PageContainer>
  );
};

export default SunoCover;
